import React, { useState, useEffect } from "react";

import { API_HOST } from "config/config";
import axios from "axios";
import useAuth from "stores/Author/AuthStore";
export default function Admin_XuLyViPham() {
    const initialHandle={
        location: "",
        vat: 10,
        totalMoney: 0,
         note: "",
    }
  const [violior, setViolior] = useState({});
  const [vehicle, setVehicle] = useState({});
  const [hanldeDate,setHandleDate]=useState(initialHandle)

  const [stateAuth] = useAuth();

  const [violiorSerch, setvioliorSerch] = useState("");
  const [vehicleSerch, setvehicleSerch] = useState("");


  useEffect(() => {
   
  }, []);
  const Search=()=>{
      (async () => {
        await axios
          .get(`${API_HOST}/citizenship-profiles/${violiorSerch}`, {
            headers: {
              Authorization: "Bearer " + stateAuth.accessToken,
            },
          })
          .then((res) => {
            setViolior(res.data)
          })
          .catch((err) =>{
            setViolior({})
            setvioliorSerch("")
              alert("Thng tin cng dn khng tn ti trong h thng.")
            });
      })();

  }
  const SearchVehilce=()=>{
    (async () => {
        await axios
          .get(`${API_HOST}/Vehicles/${vehicleSerch}`, {
            headers: {
              Authorization: "Bearer " + stateAuth.accessToken,
            },
          })
          .then((res) => {
            setVehicle(res.data)
          })
          .catch((err) =>{
            setVehicle({})
            setvioliorSerch("")
              alert("Thng tin xe khng tn ti trong h thng.")
            });
      })();


  }
  const handleOnChnage=(ent)=>{
      const {name,value}=ent.target;
      setHandleDate({
          ...hanldeDate,
          [name]: value
      })
  }
  const handleServer=()=>{
    (async () => {
        await axios
          .post(`${API_HOST}/api/v1/MarginalSanctionsTables`, 
          {
            "timeViolation": new  Date().toISOString(),
            "location": hanldeDate.location,
            "vat": hanldeDate.vat,
            "totalMoney": hanldeDate.totalMoney,
            "vehicleId": vehicle.id,
            "note": hanldeDate.note,
            "citizenShipProfileId": violior.id,
            "deadline": "2021-01-15"
          },
          {
            headers: {
              Authorization: "Bearer " + stateAuth.accessToken,
            },
           
          })
          .then((res) => {
             
              alert("X L Thnh Cng");
              window.location.reload();

          })
          .catch((err) =>{
            alert("Bn Khng C Quyn");
            });
            
      })();

  }

  return (
    <div>
         <div className="violator">
        <div className="row">
          <div className="col-4">
            <h3>Thng Tin Ngi Vi Phm</h3>
          </div>
          <div
            className="col-8"
            style={{ paddingTop: "30px", marginLeft: "auto" }}
          >
            <input
              type="text"
              onChange={(event) => {
                setvioliorSerch(event.target.value);
              }}
            />
            <button className="btn btn-primary" style={{ marginLeft: "20px" }} onClick={Search} disabled={!violiorSerch }>
              Tm Cng Dn
            </button>
          </div>
        </div>

        <div className="form-row">
          <div className="form-group col-md-2">
            <label htmlFor="inputZip">ID</label>
            <input
              type="text"
              className="form-control"
              id="inputZip"
              disabled
              value={violior.id? violior.id: ''}
            />
          </div>
          <div className="form-group col-md-6">
            <label htmlFor="inputCity">H Tn</label>
            <input
              type="text"
              className="form-control"
              id="inputCity"
              disabled
              value={violior.firstName ?`${violior?.firstName} ${violior?.lastName}`: ""}

            />
          </div>
          {/* <div className="form-group col-md-4">
            <label htmlFor="inputState">State</label>
            <select id="inputState" className="form-control">
              <option selected>Choose...</option>
              <option>...</option>
            </select>
          </div> */}
        </div>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label htmlFor="inputEmail4">Chng Minh Nhn Dn</label>
            <input
              type="email"
              className="form-control"
              id="inputEmail4"
              disabled
              value={violior.idNo ? violior.idNo: ""}

            />
          </div>
          <div className="form-group col-md-6">
            <label htmlFor="inputPassword4">Ngy Sinh</label>
            <input
              type="text"
              className="form-control"
              id="inputPassword4"
              disabled
              value={violior.dateOfBirth ?violior.dateOfBirth :""}

            />
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="inputAddress">in Thoi</label>
          <input
            type="text"
            className="form-control"
            id="inputAddress"
            disabled
            value={violior.phoneNumber ?violior.phoneNumber:""}

          />
        </div>
        <div className="form-group">
          <label htmlFor="inputAddress2">a Ch</label>
          <input
            type="text"
            className="form-control"
            id="inputAddress2"
            disabled
            value={violior.addresses ?violior.addresses:""}

          />
        </div>
      </div>
      <div className="vehicle">
        <div className="row">
          <div className="col-4">
            <h3>Thng Tin Xe</h3>
          </div>
          <div
            className="col-8"
            style={{ paddingTop: "30px", marginLeft: "auto" }}
          >
            <input
              type="text"
              onChange={(event) => {
                setvehicleSerch(event.target.value);
              }}
            />
            <button className="btn btn-primary" style={{ marginLeft: "20px" }} onClick={SearchVehilce} disabled={!vehicleSerch }>
              Tm Xe
            </button>
          </div>
        </div>

        <div className="form-row">
          <div className="form-group col-md-2">
            <label htmlFor="inputZip">Id</label>
            <input
              type="text"
              className="form-control"
              id="inputZip"
              disabled
              value={vehicle.id? vehicle.id: ''}
            />
          </div>
          <div className="form-group col-md-6">
            <label htmlFor="inputCity">S Khung</label>
            <input
              type="text"
              className="form-control"
              id="inputCity"
              disabled
              value={vehicle.frameNumber ?`${vehicle?.frameNumber}`: ""}

            />
          </div>
        
        </div>
        <div className="form-row">
          <div className="form-group col-md-6">
            <label htmlFor="inputEmail4">Mu</label>
            <input
              type="email"
              className="form-control"
              id="inputEmail4"
              disabled
              value={vehicle.color ? vehicle.color: ""}

            />
          </div>
          <div className="form-group col-md-6">
            <label htmlFor="inputPassword4">Hng</label>
            <input
              type="text"
              className="form-control"
              id="inputPassword4"
              disabled
              value={vehicle.brand ?vehicle.brand :""}

            />
          </div>
        </div>
        <div className="form-group">
          <label htmlFor="inputAddress">Bin S</label>
          <input
            type="text"
            className="form-control"
            id="inputAddress"
            disabled
            value={vehicle.numberPlate ?vehicle.numberPlate:""}

          />
        </div>
      
      </div>
  

      <div className="vehicle">
        <div className="row">
            <h3>Thng Tin X Pht</h3>
        
        </div>
        <div>
  <div className="form-row">
    <div className="form-group col-md-6">
      <label htmlFor="inputEmail4">V Tr Vi Phm</label>
      <input type="email" className="form-control" id="inputEmail4" name="location" onChange={handleOnChnage}  />
    </div>
    <div className="form-group col-md-2">
      <label htmlFor="inputPassword4">VAT</label>
      <input type="number" className="form-control" id="inputPassword4" min="0" max="100" name="vat" onChange={handleOnChnage} defaultValue={initialHandle.vat}  />
    </div>
  </div>
  <div className="form-group">
  <label htmlFor="exampleFormControlTextarea1">Hnh vi Vi Phm</label>
  <textarea className="form-control" id="exampleFormControlTextarea1" rows={3} name="note"  onChange={handleOnChnage} />
  </div>
 
  <div className="form-row">
    {/* <div className="form-group col-md-4">
      <label htmlFor="inputCity">Thi Gian Vi Phm</label>
      <input type="datetime-local" id="inputCity" className="form-control" />

    </div>
    <div className="form-group col-md-4">
      <label htmlFor="inputState">Thi Gian Np Pht</label>
      <input type="date" className="form-control" id="inputState" name="birthday"  />


    </div> */}
    <div className="form-group col-md-4">
    <label htmlFor="inputPassword4">Tng Tin Pht</label>
      <input type="number" className="form-control" id="inputPassword4" min="0" name="totalMoney"   onChange={handleOnChnage} defaultValue={initialHandle.totalMoney} />


    </div>
   
  
  </div>
  <div className="footer" style={{marginLeft:'auto'}} >
        <button className="btn btn-success"  disabled={  
          // eslint-disable-next-line 
          hanldeDate.note ==="" || hanldeDate.location=== "" || !vehicle.id && !violior.id || hanldeDate.totalMoney===0} onClick={handleServer} >X L</button>

    </div>
 
</div>
     
      </div>
  
  
    </div>
  );
}
